//src/store/UserStore.ts

import { defineStore } from "pinia";

import { MeetContainDTO } from "@/arq_hex/settle/_domain/dto/MeetContainDTO";
import { UserLogin } from "@/arq_hex/gral/_domain/dto/UserLogin";
import { getUniqUuid } from "@/arq_hex/_utils/UtilsNumber";
import { executeSignIn, executeSignUp } from "@/arq_hex/_utils/axios/AuthRequest";
import { useMeetStore } from "@/arq_hex/settle/_domain/store/MeetStore";
import { useUserStore } from "./UserStore";
import { useBillStore } from "@/arq_hex/settle/_domain/store/BillStore";
import { MeetDTO } from "@/arq_hex/settle/_domain/dto/MeetDTO";
import { BillDTO } from "@/arq_hex/settle/_domain/dto/BillDTO";
import { isRunningServer } from "@/arq_hex/_utils/axios/AxiosGral";
import { ErrorDto } from "../dto/ErrorDto";

const defaultUser: UserLogin = { idUser: getUniqUuid(), email: undefined, name: undefined, password: undefined, token: undefined, roles: [], isGuest: false, isAllowedSave:false };

export const useUserLoginStore = defineStore('userLogin', {
    state: () => {
        return {
            userStore: defaultUser
        }
    },
    getters: {

    },
    actions: {
        setUserLoginToStore(user: UserLogin) {

            //Agrego el usuario logueado al sistema
            this.userStore = user;

            //Agrego el usuario autenticado al listado de Usuarios
            useUserStore().resetUserStore();
            const userSimple = { idUser: getUniqUuid(), name: user.name as string };
            useUserStore().addUserStore(userSimple);

            //Cargo los encuentros
            //Meet Activo
            const activesFilter: MeetDTO[] | undefined = user.settle?.listMeet.filter(m => m.active);
            const actives = activesFilter && activesFilter.length > 0 ? activesFilter : [];
            //Historial
            const historialFilter: MeetDTO[] | undefined = user.settle?.listMeet.filter(m => !m.active);
            const listHistorial = historialFilter ? historialFilter : [];

            const listMeet: MeetContainDTO = { meetActive: new MeetDTO(), activesMeetList: actives, historyMeetList: listHistorial };

            // Agrego los encuentros abiertos y cerrados
            useMeetStore().meetStore = listMeet;
        },

        async initSettleUserLogin(meet: MeetDTO) {
            //Inicializo la estructura segun el encuentro elegido
            //Marco como encuentro activo
            useMeetStore().meetStore.meetActive = meet;

            //Cargo los Pagos
            useBillStore().billStore = meet.listBill;

            //Agrego a los usuarios que participaron en los pagos
            useUserStore().userStore = useBillStore().getUserBillMeetStore(useUserStore().userStore, meet.listBill);
        },

        async initSettleNewMeetUserLogin(meet: MeetDTO) {
            if (this.userStore.settle && !this.userStore.settle.listMeet) {
                this.userStore.settle.listMeet = new Array<MeetDTO>();
            }

            //Agrego los pagon
            meet.listBill = useBillStore().billStore as BillDTO[];

            //Marco como encuentro activo
            useMeetStore().meetStore.meetActive = meet;

            //Como es un encuentro nuevo agrego al listado de activos
            useMeetStore().meetStore.activesMeetList.push(meet);
        },

        resetUserLoginStore() {
            this.userStore = defaultUser;
        },

        async loginUserServer(user: UserLogin) {
            if (await isRunningServer()) {
                return executeSignIn(user);
            } else {
                if (user.isGuest) {
                    //Si es Guest y no hay conexion devuelvo el creado automaticamente.
                    return user;
                } else {
                    return new ErrorDto('Network Error!.', 0);
                }
            }
        },

        async registerUserServer(user: UserLogin) {
            if (await isRunningServer()) {
                return executeSignUp(user);
            } else {
                if (user.isGuest) {
                    //Si es Guest y no hay conexion devuelvo el creado automaticamente.
                    return user;
                } else {
                    return new ErrorDto('Network Error!.', 0);
                }
            }
        },

        resetUserLogin() {
            this.userStore = defaultUser;
        }
    }

});



