//src/_domain/settle/store/MeetStore.ts

// https://stackoverflow.com/questions/71452197/how-to-set-up-pinia-getter-in-vue-3-composition-api

import CloneObj from "just-clone";
import { Ref, ref } from "vue";
import { defineStore } from "pinia";

import AlertsUtil from "@/arq_hex/_utils/alerts/AlertsUtil";
import { MeetContainDTO } from "@/arq_hex/settle/_domain/dto/MeetContainDTO";
import { MeetDTO } from '@/arq_hex/settle/_domain/dto/MeetDTO';
import { executeRemoveMeetServer, executeUpdateMeetServer, executeAddMeetServer } from "@/arq_hex/_utils/axios/MeetRequest";
import { useUserLoginStore } from "@/arq_hex/gral/_domain/store/UserLoginStore";
import { ErrorDto } from "@/arq_hex/gral/_domain/dto/ErrorDto";
import { isRunningServer } from "@/arq_hex/_utils/axios/AxiosGral";
import { useUserStore } from "@/arq_hex/gral/_domain/store/UserStore";
import { useBillStore } from "@/arq_hex/settle/_domain/store/BillStore";

const defaultMeetContain = { meetActive: new MeetDTO(), historyMeetList: [], activesMeetList: [] };

export const useMeetStore = defineStore('MeetStore', () => {
    //Using Composition API
    //State
    const meetStore: Ref<MeetContainDTO> = ref(defaultMeetContain); //[] as MeetDTO[]

    //Getters
    /*
    const getMeetByNameStore = computed((name: string) => {
        return meetStore.filter((obj) => obj.name === name);
    })
    */

    //Actions
    async function createMeetStore(meet: MeetDTO) {
        meetStore.value.meetActive = meet;

        //Envio al servidor el encuentro creado
        const userLogin = useUserLoginStore().userStore;
        if (await isRunningServer()) {
            executeAddMeetServer(userLogin.email as string, meet, userLogin.token as string)
                .then((response) => {
                    const error = response as ErrorDto;
                    if (error.message) {
                        AlertsUtil.toastMessage(error.message as string);
                    }
                });
        } else {
            if (!userLogin.isGuest) {
                useUserLoginStore().userStore.isAllowedSave = true;
                AlertsUtil.toastNotNetwork();
            }
        }

        //Devuelvo el encuentro creado para inicializar la estructura del usuario logueado.
        return meet;
    }

    async function updateMeetStore(meet: MeetDTO) {
        //Actualizo el encuentro que estoy trabando
        meetStore.value.meetActive = meet;

        //Tambien actualizo en el listado de activos para que se refleje el cambio.
        meetStore.value.activesMeetList = meetStore.value.activesMeetList.map(obj => obj.idMeet === meet.idMeet ? meet : obj);

        //Modificar el encuentro en el servidor.
        const userLogin = useUserLoginStore().userStore;
        if (await isRunningServer()) {

            executeUpdateMeetServer(userLogin.email as string, meet, userLogin.token as string)
                .then((response) => {
                    const error = response as ErrorDto;
                    if (error.message) {
                        AlertsUtil.toastMessage(error.message as string);
                    }
                });
        } else {
            if (!userLogin.isGuest) {
                useUserLoginStore().userStore.isAllowedSave = true;
                AlertsUtil.toastNotNetwork();
            }
        }
    }

    async function closeMeetStore() {
        //Recupero el encuentro activo
        const meet = meetStore.value.meetActive;
        meet.active = false;
        //Actualizo la hora de modificacion.
        meet.updated = new Date();

        // Reseteo encuentro activo
        meetStore.value.meetActive = new MeetDTO();
        // Lo quito del listado de activos
        meetStore.value.activesMeetList = meetStore.value.activesMeetList.filter(m => m.idMeet !== meet.idMeet);
        // Paso el encuentro a la lista de historial
        meet.listBill = useBillStore().billStore;
        meetStore.value.historyMeetList.push(meet);

        //Hago una copia porque debo resetear los pagos.
        const meetToSend = CloneObj(meet);
        //Para que no viaje datos que ya estan persistidos en el servidor.
        meetToSend.listBill = [];

        //Reflejo el cambio de estado en el servidor.
        const userLogin = useUserLoginStore().userStore;
        if (await isRunningServer()) {
            executeUpdateMeetServer(userLogin.email as string, meetToSend, userLogin.token as string)
                .then((response) => {
                    const error = response as ErrorDto;
                    if (error.message) {
                        AlertsUtil.toastMessage(error.message as string);
                    }
                });
        } else {
            if (!userLogin.isGuest) {
                useUserLoginStore().userStore.isAllowedSave = true;
                AlertsUtil.toastNotNetwork();
            }
        }
        //Elimino los gastos hechos
        useBillStore().resetBillStore();

        return true;
    }

    async function removeMeetServer(id_meet: string): Promise<boolean> {

        const userLogin = useUserLoginStore().userStore;

        if (await isRunningServer()) {

            executeRemoveMeetServer(userLogin.email as string, id_meet, userLogin.token as string)
                .then((response) => {
                    const error = response as ErrorDto;
                    if (error.message) {
                        AlertsUtil.toastMessage(error.message as string);
                        return false;
                    }
                });
        } else {
            if (!userLogin.isGuest) {
                useUserLoginStore().userStore.isAllowedSave = true;
                AlertsUtil.toastNotNetwork();
                return false;
            }
        }
        return true;
    }

    async function removeMeetActivesStore(id_meet: string) {
        //Remuevo del servidor
        if (await removeMeetServer(id_meet)) {
            const indexOfList = meetStore.value.activesMeetList?.findIndex(obj => obj.idMeet === id_meet);
            meetStore.value.activesMeetList.splice(indexOfList, 1);
            //Reseteo lista de usuarios
            useUserStore().resetUserStore();
        }
    }

    async function removeMeetHistorialStore(id_meet: string) {
        //Remuevo del servidor
        if (await removeMeetServer(id_meet)) {
            meetStore.value.historyMeetList = meetStore.value.historyMeetList.filter(obj => obj.idMeet !== id_meet);
            //Reseteo lista de usuarios
            useUserStore().resetUserStore();
        }
    }

    function resetMeets() {
        meetStore.value = defaultMeetContain;
    }

    //Utilizada por el LogOut por si no se actualiz (error network) en el servidor el encuentro activo.
    //NOTA: Solo persiste el ultimo encuentro activo trabajado.
    function saveCurrentStateUserLogin() {

        //Actualizo la hora de modificacion.
        const meet = useMeetStore().meetStore.meetActive
        meet.updated = new Date();
        meet.listBill = useBillStore().billStore;

        const userLogin = useUserLoginStore().userStore
        if (userLogin.isAllowedSave && userLogin.token) {
            // Marco como que ya fue actualizado el estado en el servidor
            userLogin.isAllowedSave = false;
            executeUpdateMeetServer(userLogin.email as string, meet, userLogin.token as string)
                .then((response) => {
                    const error = response as ErrorDto;
                    if (error.message) {
                        userLogin.isAllowedSave = true;

                    }
                })
        }
    }

    return { meetStore, resetMeets, createMeetStore, closeMeetStore, updateMeetStore, removeMeetActivesStore, removeMeetHistorialStore, saveCurrentStateUserLogin };

});
