//

import { executeDELETE, executePOST, executePUT } from "./AxiosGral";
import { BillDTO } from "@/arq_hex/settle/_domain/dto/BillDTO";

export const executeAddBillServer = async (idUser: string, idMeet: string, bill: BillDTO, token: string) => {

    const payload = {
        idUser,
        idMeet,
        bill
    }
    const body = JSON.stringify(payload);

    return await executePOST('/settle/bill', body, token);
};

export const executeUpdateBillServer = async (idUser: string, idMeet: string, bill: BillDTO, token: string) => {

    const payload = {
        idUser,
        idMeet,
        bill
    }
    const body = JSON.stringify(payload);

    return await executePUT('/settle/bill', body, token);
};

export const executeRemoveBillServer = async (idUser: string, idMeet: string, idBill: string, token: string) => {

    const payload = {
        idUser,
        idMeet,
        bill: {
            idBill
        }
    }
    const data = JSON.stringify(payload);

    return await executeDELETE('/settle/bill', data, token);
};
