//

import { executeDELETE, executePOST, executePUT } from "./AxiosGral";
import { MeetDTO } from "@/arq_hex/settle/_domain/dto/MeetDTO";

export const executeAddMeetServer = async (idUser: string, meet: MeetDTO, token: string) => {

    const payload = {
        idUser: idUser,
        meet: meet
    }
    const body = JSON.stringify(payload);

    return await executePOST('/settle/meet', body, token);
};

export const executeUpdateMeetServer = async (idUser: string, meet: MeetDTO, token: string) => {

    const payload = {
        idUser: idUser,
        meet: meet
    }
    const body = JSON.stringify(payload);

    return await executePUT('/settle/meet', body, token);
};

export const executeRemoveMeetServer = async (idUser: string, idMeet: string, token: string) => {

    const payload = {
        idUser,
        meet: {
            idMeet
        }
    }

    const data = JSON.stringify(payload);

    return await executeDELETE('/settle/meet', data, token);
};