//src/store/UserStore.ts

import { defineStore } from "pinia";

import { UserDTO } from "@/arq_hex/gral/_domain/dto/UserDTO";
import { useBillStore } from "@/arq_hex/settle/_domain/store/BillStore";
//import { getUniqUuid } from "@/arq_hex/_utils/UtilsNumber";

export const useUserStore = defineStore('UserStore', {
    state: () => {
        return {
            userStore: new Array<UserDTO>()
        }
    },
    //Getters
    getters: {
        getUserByName: (state) => {
            return (nameUser: string) => state.userStore.find(obj => obj.name === nameUser);
        }
    },
    //Modifica estados
    actions: {
        isUserInUse(idUser: string): boolean {
            const listUserInBill = useBillStore().getUserBillMeetStore([], useBillStore().billStore);
            const isInUse = listUserInBill.find(u => u.idUser === idUser)
            return isInUse ? true : false;
        },
        async addUserStore(user: UserDTO) {
            this.userStore.push(user);
        },
        async updateUserStore(user: UserDTO) {
            const indexOfUser = this.userStore.findIndex(obj => obj.idUser === user.idUser);
            this.userStore[indexOfUser] = user;
        },
        async removeUserStore(idUser: string) {
            const indexOfUser = this.userStore.findIndex(obj => obj.idUser === idUser);
            this.userStore.splice(indexOfUser, 1);
        },
        resetUserStore() {
            this.userStore = new Array<UserDTO>();
        }
    },
});
